@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

html, body {
    margin: 0;
    padding: 0;
    font-family: 'Montserrat';
}

body {
    background: #1B1E22;
    color: #FDFFFC;
}

nav {
    font-size: 1.8em;
    height: 90px;
    background: #6F73D2;
    margin-bottom: 75px;
}

nav > ul {
    text-align: center;
    padding-right: 15px;
    margin-block-start: 0;
    margin-block-end: 0;
    padding-top: 25px;
}

.quote {
    text-shadow: 10px 5px 5px #6F73D2;
    text-align: center;
}

.welcome {
    text-shadow: 10px 5px 5px #6F73D2;
    text-align: center;
    font-size: 38px;
    
}

.emoji {
    font-size: 70px;
    line-height: .15;
    text-align: center;
}

.journal {
    line-height: 1.8;
    text-align: center;
    margin-bottom: 40px;
}

.textarea {
    display: block;
    margin-left: auto;
    margin-right: auto;
    border: 5px solid #6F73D2;
    border-radius: 10px;
}

#submit {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 25px;
    padding: 12px;
    width: 130px;
    font-size: .98em;
    font-weight: 600;
    background: #FDFFFC;
    border: 4px solid #6F73D2;
    border-radius: 10px;
}

#submit:hover {
    background: #6F73D2;
    color: #FDFFFC;
}

#gif {
    display: block;
    margin-left: auto;
    margin-right: auto;
    border-radius: 45%;
}

select {
    appearance: none;
    background: #6F73D2;
    color: #FDFFFC;
    border: none;
    padding: 0 1em 0 0;
    margin: 0;
    font-family: inherit;
    font-size: inherit;
    font-weight: 700;
    cursor: inherit;
    line-height: inherit;
    outline: none;
}

.select {
    display: block;
    margin-left: auto;
    margin-right: auto;
    max-width: 14.5ch;
    border: 1px solid #777;
    border-radius: .25em;
    padding: .25em .5em;
    font-size: 1.25rem;
    cursor: pointer;
    line-height: 1.1;
    background-color: #6F73D2;
}

#play-btn, #pause-btn {
    width: 100px;
    height: 100px;
    background: radial-gradient( #6F73D2 60%, rgba(255, 255, 255, 1) 62%);
    border-radius: 50%;
    position: relative;
    display: block;
    margin: 100px auto;
    box-shadow: 0px 0px 25px 5px #6F73D2;
    outline: none;
    font-size: 26px;
    color: #FDFFFC;
    font-weight: 700;
    cursor: pointer;
}

/* triangle */
#play-btn::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translateX(-40%) translateY(-50%);
  transform: translateX(-40%) translateY(-50%);
  transform-origin: center center;
  width: 0;
  height: 0;
  border-top: 15px solid transparent;
  border-bottom: 15px solid transparent;
  border-left: 25px solid #fff;
  z-index: 100;
  -webkit-transition: all 400ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
  transition: all 400ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

/* pulse wave */
#play-btn:before, #pause-btn:before {
  content: "";
  position: absolute;
  width: 150%;
  height: 150%;
  -webkit-animation-delay: 0s;
  animation-delay: 0s;
  -webkit-animation: pulsate1 2s;
  animation: pulsate1 2s;
  -webkit-animation-direction: forwards;
  animation-direction: forwards;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -webkit-animation-timing-function: steps;
  animation-timing-function: steps;
  opacity: 1;
  border-radius: 50%;
  border: 5px solid rgba(255, 255, 255, .75);
  top: -30%;
  left: -30%;
  background: rgba(198, 16, 0, 0);
}

@-webkit-keyframes pulsate1 {
  0% {
    -webkit-transform: scale(0.6);
    transform: scale(0.6);
    opacity: 1;
    box-shadow: inset 0px 0px 25px 3px rgba(255, 255, 255, 0.75), 0px 0px 25px 10px rgba(255, 255, 255, 0.75);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 0;
    box-shadow: none;

  }
}

@keyframes pulsate1 {
  0% {
    -webkit-transform: scale(0.6);
    transform: scale(0.6);
    opacity: 1;
    box-shadow: inset 0px 0px 25px 3px rgba(255, 255, 255, 0.75), 0px 0px 25px 10px rgba(255, 255, 255, 0.75);
  }
  100% {
    -webkit-transform: scale(1, 1);
    transform: scale(1);
    opacity: 0;
    box-shadow: none;

  }
}



